import React, { useEffect, useLayoutEffect, useState } from "react";
import {
  Paper,
  Container,
  Typography,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button,
  Snackbar,
  useTheme,
  useMediaQuery,
  Grid,
  CircularProgress,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Box } from "@mui/system";
import { MediumTitle } from "../../../shared/Typography";
import { useFormik } from "formik";
import {
  createInternal,
  getInternalCommission,
  setAlert,
} from "../../../redux/reducers/internal";
import { useDispatch, useSelector } from "react-redux";
import { Alert } from "@mui/material";
import { useHistory } from "react-router";
import { WhiteBox } from "../../../shared/Boxes";
import CommissionIcon from "../../../assets/images/state/commissionDark.svg";

const SendMoney = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const theme = useTheme();
  const mobile = useMediaQuery(theme.breakpoints.down("md"));

  const [balance, setBalance] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const [sum, setSum] = useState("");

  const firstUpdate = React.useRef(true);

  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );
  const { createInternalData, isAlertOpen, internalCommission } = useSelector(
    ({ internalReducer }) => internalReducer
  );

  const formik = useFormik({
    initialValues: {
      sum: "",
      currency: "",
      recipient: "",
      security_code: "",
    },
    onSubmit: (values) => {
      setLoading(true);
      dispatch(createInternal(values));
      dispatch(setAlert(true));
    },
  });

  const withdrawCoin = СurrenciesList.filter(
    (item) => item.currency === formik.values.currency
  )[0];

  useEffect(() => {
    dispatch(getInternalCommission());
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (СurrenciesList.length) {
      const usdt = СurrenciesList.filter((item) => item.currency === "USDT");

      formik.setFieldValue("currency", usdt[0]?.currency);
    }
    // eslint-disable-next-line
  }, [СurrenciesList]);

  useEffect(() => {
    const balance = СurrenciesList?.find(
      // eslint-disable-next-line eqeqeq
      (item) => item.currency == formik.values.currency
    );

    setBalance(balance);
    // eslint-disable-next-line
  }, [formik.values.currency]);

  useEffect(() => {
    const sumWithCommission = () => {
      const commission =
        (formik.values.sum / 100) * internalCommission?.commission;
      return formik.values.sum - commission;
    };

    if (formik.values.sum) {
      setSum(sumWithCommission().toFixed(withdrawCoin.decimal));
    }
    // eslint-disable-next-line
  }, [formik.values.sum, formik.values.currency]);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    // eslint-disable-next-line
    if (createInternalData.status == 201) {
      setMessage("Средства успешно отправлены!");
      setTimeout(() => {
        history.push("/dashboard/history");
        setAlert(false);
      }, 1000);
    } else if (createInternalData?.data?.detail) {
      setMessage(createInternalData?.data?.detail);
    } else if (
      createInternalData?.data?.security_code &&
      // eslint-disable-next-line
      createInternalData.status == 400
    ) {
      setMessage(createInternalData?.data?.security_code);
    }
    setLoading(false);

    // eslint-disable-next-line
  }, [createInternalData]);

  return (
    <Paper component="section">
      <Container maxWidth="lg">
        <Snackbar
          open={isAlertOpen}
          autoHideDuration={3000}
          onClose={() => {
            dispatch(setAlert(false));
          }}
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
        >
          <Alert
            // eslint-disable-next-line
            severity={createInternalData.status == 201 ? "success" : "error"}
            // eslint-disable-next-line
            color={createInternalData.status == 201 ? "success" : "error"}
            variant="filled"
          >
            {message}
          </Alert>
        </Snackbar>
        <form onSubmit={formik.handleSubmit} autoComplete="off">
          <Typography
            variant="h3"
            style={{
              textAlign: "center",
              color: "#191D23",
              marginBottom: 24,
            }}
          >
            {t("sendFunds")}
          </Typography>
          <Grid container spacing={2} justifyContent="center">
            <Grid xs={12} sm={6} md={3} item>
              <Box
                className={classes.whitebox}
                style={{ height: 210, flexDirection: "column" }}
              >
                <img className="icon-lg" src={CommissionIcon} alt="" />
                <Typography variant="h5" className={classes.subtitle}>
                  {t("DueDate")}
                </Typography>
                <Typography variant="h4" className={classes.titlee}>
                  {t("Instantly")}
                </Typography>
              </Box>
            </Grid>
            <Grid
              xs={12}
              sm={6}
              md={3}
              item
              style={{ marginBottom: mobile && 32 }}
            >
              <WhiteBox
                className={classes.whitebox}
                style={{ height: 210, flexDirection: "column" }}
              >
                <img className="icon-lg" src={CommissionIcon} alt="" />
                <Typography variant="h5" className={classes.subtitle}>
                  {t("OurCommission")}
                </Typography>
                <Typography variant="h4" className={classes.titlee}>
                  {internalCommission?.commission} %
                </Typography>
              </WhiteBox>
            </Grid>
          </Grid>
          <Box mb={"16px"}>
            <MediumTitle>{t("userLogin")}</MediumTitle>
            <TextField
              fullWidth
              label={t("enterUserLogin")}
              variant="outlined"
              type="email"
              name="recipient"
              required
              onChange={formik.handleChange}
              values={formik.values.recipient}
              className={classes.input}
            />
          </Box>
          <Box className={classes.box}>
            {!mobile && <MediumTitle>{t("currencyAmount")}</MediumTitle>}
            <Box
              sx={{
                display: "flex",
                alignItems: "baseline",
                cursor: "pointer",
                "&:hover": {
                  "& span": {
                    borderBottom: "2px solid #000",
                  },
                },
              }}
              onClick={() => {
                formik.setFieldValue("sum", balance?.balance);
              }}
            >
              <Typography className={classes.title}>
                {t("availableAmount")}&#160;
              </Typography>
              <Box
                component="span"
                sx={{
                  fontWeight: 500,
                  fontSize: { xs: 19, md: 23 },
                  lineHeight: "23px",
                  // color: "#191D23",
                  transition: "all 0.2s linear",
                  borderBottom: "1px solid transparent",
                }}
              >
                {balance?.balance}&#160;
              </Box>
              <Box
                component="p"
                sx={{
                  fontWeight: 500,
                  fontSize: { xs: 19, md: 23 },
                  lineHeight: "23px",
                  color: "#191D23",
                }}
              >
                {balance?.currency}
              </Box>
            </Box>
          </Box>
          <Box
            sx={{
              display: "flex",
              flexDirection: { xs: "column", md: "row" },
            }}
          >
            <FormControl fullWidth className={classes.formControl}>
              <InputLabel>{"Валюта"}</InputLabel>
              <Select
                required
                label={"Валюта"}
                name="currency"
                value={formik.values.currency}
                variant="outlined"
                onChange={formik.handleChange}
              >
                {СurrenciesList.map((item, idx) => (
                  <MenuItem
                    key={idx}
                    aria-label="None"
                    style={{ display: "flex" }}
                    value={item.currency}
                  >
                    <img src={item.logo} alt="" />
                    {item.name} ({item.balance})
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <TextField
              fullWidth
              label={t("AmountDispatch")}
              name="sum"
              required
              type="text"
              InputProps={{
                inputProps: {
                  pattern: "\\d*.\\d*",
                },
              }}
              onKeyPress={(event) => {
                if (!/[0-9.]/.test(event.key)) {
                  event.preventDefault();
                }
              }}
              value={formik.values.sum}
              onChange={formik.handleChange}
              variant="outlined"
              className={classes.input}
            />
            <TextField
              fullWidth
              label={t("AmountToCredited")}
              disabled
              style={{ marginLeft: !mobile && 16 }}
              value={!formik.values.sum ? "" : sum}
              variant="outlined"
              className={classes.input}
            />
          </Box>
          <Box mt={"16px"}>
            <MediumTitle>{t("protectionCode")}</MediumTitle>
            <TextField
              fullWidth
              label={t("enterProtectionCode")}
              required
              type="number"
              variant="outlined"
              name="security_code"
              values={formik.values.security_code}
              onChange={formik.handleChange}
              className={classes.input}
            />
          </Box>
          <Button
            size="large"
            variant="contained"
            disabled={loading}
            color="primary"
            type="submit"
            className={classes.button}
          >
            {loading ? <CircularProgress color="secondary" /> : t("Continue")}
          </Button>
        </form>
      </Container>
    </Paper>
  );
};

export default SendMoney;

const useStyles = makeStyles((theme) => ({
  input: {
    marginTop: 16,
  },

  formControl: {
    marginTop: 16,
    marginRight: 16,

    "& .MuiInputLabel-formControl": {
      left: 14,
    },

    "& .MuiInputLabel-shrink": {
      left: 0,
    },
  },

  box: {
    display: "flex",
    justifyContent: "space-between",
    [theme.breakpoints.down(992)]: {
      flexDirection: "column",
    },
  },

  title: {

    fontSize: 23,
    fontWeight: "300",
    [theme.breakpoints.down(992)]: {
      marginTop: 8,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 19,
    },
  },

  button: {
    margin: "30px auto",
    display: "block",
    [theme.breakpoints.down(992)]: {
      width: "100%",
    },
  },

  subtitle: {
    fontWeight: 300,
    textAlign: "center",
    margin: "22px 0 8px",
  },

  titlee: {
    fontWeight: 600,
    marginBottom: 16,
    fontSize: 23,
    [theme.breakpoints.down("md")]: {
      fontSize: 23,
    },
  },

  whitebox: {
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: 8,
    background: "#FFF",
    height: 167,
    display: "flex",
    justifyContent: "flex-start",
    alignItems: "center",
    padding: "30px",
    transition: "all 0.2s linear",
    "&:hover": {
      boxShadow: "0px 0px 6px 2px #E2E8F0",
    },
    [theme.breakpoints.down(576)]: {
      display: "flex",
      flexDirection: "column",
    },
  },
}));
